import uniCollapse from '@/colorui/components/uni-collapse.vue'
import uniCollapseItem from '@/colorui/components/uni-collapse-item.vue'
import misEnum from '../../common/mis-enum.js';
export default {
	components: {
		uniCollapse,
		uniCollapseItem
	},
	data() {
		return {
			stars: [1, 2, 3, 4, 5],
			info: {},
			list: []
		}
	},
	onLoad(option) {
		var info = JSON.parse(decodeURIComponent(option.para));
		this.info = info;
		//获取派车单
		if (this.info.status >= 2) {
			global.$http.post('/car/dispatch/getListByDispatch', {
				params: {
					order_code: this.info.order_code
				},
			}).then(res => {
				if (res.status === "0") {
					this.list = res.data;
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		}
	},
	methods: {
		getCarListData() {
			this.status = 'loading';
			global.$http.post('/car/dispatch/dispatchCarList', {
				params: {
					order_code: this.info.order_code,
					page: this.page,
					pageSize: this.pageSize
				},
			}).then(res => {
				if (res.status === "0") {
					this.status = 'noMore';
					this.cars = res.data;
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		},
		getDriverListData() {
			this.status = 'loading';
			global.$http.post('/car/dispatch/dispatchDriverList', {
				params: {
					order_code: this.info.order_code,
					page: this.page,
					pageSize: this.pageSize
				},
			}).then(res => {
				if (res.status === "0") {
					this.status = 'noMore';
					this.drivers = res.data;
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		},
		getTitle: function(totalFee) {
			return "总费用(元)：" + totalFee;
		},
		getStatusStr: function(status) {
			return misEnum.DispatchRecordEnumMap.get(status);
		}
	}
}
